/*
  common.scss
*/
@font-face {
  font-family: 'icons-turn-arrow';
  src: url('../icon/turn-arrow.eot') format('enbedded'),
       url('../icon/turn-arrow.woff') format('woff'),
       url('../icon/turn-arrow.ttf') format('truetype'),
       url('../icon/turn-arrow.svg') format('svg');
}

body {
  margin: 0;
  padding: 0;
  overflow-y: hidden;
}

h1,h2,h3,p {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: #FFF;
}

.clearFix {
  zoom: 1;
  &:before,&:after {
    display: block;
    content: '';
    visibility: hidden;
    clear: both;
  }
}

.App {
  width: 100%;
  height: 100vh;
  background-color: #282c34;
}